import React from 'react'

function AboutMe({hideAboutMe}) {

  const onProjectsClick = () =>{
    hideAboutMe()
    const el = document.getElementById('projects')
    if (el) {    
      el.scrollIntoView({behavior: "smooth"})
    }
  }

  return (
    <div className="overlay aboutMe">
      <div className="drawer">
          <div className="hideOverlay"><img onClick={hideAboutMe} src="./img/deleteButton.svg" alt="back" /></div>
          <div className="d-flex flex justify-center"><h1>About me</h1></div>
          <div className="content d-flex justify-center align-center flex-wrap"> 
            <div>
                <img className="d-flex flex flex-wrap" width={300} height={480} src="./img/aboutMe/aboutMe.jpg"  alt="img" />

                <div className="cardButtons d-flex flex justify-center">
                  <div className="aboutMeButtons d-flex flex justify-center">
                    <a href="https://github.com/Sa6aTeur" target="_blank"><button className="githubButton">To gitHub</button></a>
                    <button onClick={onProjectsClick} className="githubButtonGreen">To Projects</button>                         
                  </div>       
                </div>
            </div>
            <div className=" resume d-flex align-start justify-between pt-0">
            <ul>
              <li>
                <p><b>Name:</b> Dubravin Daniil </p>       
              </li>
              <li>     
                <p><b>Age:</b> 20</p>
              </li>
              <li>     
                <p><b>Location:</b>  Republic of Tatarstan, Kazan</p>
              </li>
              <li>
                <p><b>Education:</b>  KFU, Information Security</p>
              </li>
              <li>
                <p><b>Languages:</b>  Russian, English (B1: Pre-intermediate)</p>
              </li>
              <li>
                <p><b>My Experience:</b> Internship in the company IT-Produce (PHP). Also you can check my projects on github or start them in project card on home page.</p>
              </li>
              <li>
                <p><b>My way to IT:</b> I started studying programming with C++ and C# , then I studied html and became interested. I made several landing pages and then learned about PHP engines. I was doing an internship at an IT company, at that time I got acquainted with React and JS and I was fascinated by it. At the moment I want to develop in the Frontend direction and I also want to eventually become a Full-stack developer </p>
              </li>

            </ul>
            </div>
          </div>  
      </div>
    </div>
  )
}

export default AboutMe

