import React from 'react'

function WorkCard({onCardClick, logoUrl, title, fullDescription,description, images, gitHubLink, projectLink, technologies}) {

  return (
    <div onClick={() => onCardClick({title, fullDescription, images, gitHubLink, projectLink, technologies})} className="card">
      <div className="title"><h2>{title}</h2></div>
      <div className="cardImageWrapper">
          <img width={310} height={240} className="cardImage" src={logoUrl} alt="cardImage" />
      </div>
      <div className="description"><p>{description}</p></div>
    </div>
  )
}

export default WorkCard
