import React from 'react'

function Contacts({hideContacts}) {
  return (
    <div className="overlay contacts">
    <div className="drawer">
        <div className="hideContacts"><img width={30} height={30} onClick={hideContacts} src="./img/deleteButton.svg" alt="back" /></div>
        <h1>My Contacts</h1>
        <ul className="d-flex align-center justify-between">
            <li ><a href="https://vk.com/dantist.aka.dane4ka" target="_blank" > <img src="./img/contactsIcons/vkIcon.svg" width={50} height={50} alt="" /> </a></li>
            <li ><a href="https://www.instagram.com/dubravin.danya/" target="_blank" > <img src="./img/contactsIcons/instIcon.svg" width={50} height={50} alt="" /> </a></li>
            <li ><a href="https://github.com/Sa6aTeur" target="_blank" > <img src="./img/contactsIcons/gitIcon.svg" width={50} height={50} alt="" /> </a></li>
          </ul>
    </div>
  </div>
  )
}

export default Contacts
