import React from 'react'

function Header({onAboutMeClick, onContactsClick}) {
  return (
    <div>
      <header className="header d-flex align-center flex-wrap justify-between">
        <div className="logo">
          <img width={180} height={40} src="./img/logo.svg" alt="logo" />
        </div>

        <div className="headerNav">
          <ul className="d-flex align-center justify-between">
            <li onClick={onAboutMeClick} className="ml-25">About Me</li>
            <li onClick={onContactsClick} className="ml-25">Contacts</li>
          </ul>
        </div>      
      </header>
    </div>
  )
}

export default Header
