import React from 'react'
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/custom-animations/open-animation.css';
import AwesomeSliderStyles from 'react-awesome-slider/src/styles';

function CardOverlay({title,technologies, fullDescription, images, gitHubLink, projectLink,hideOverlay}) {

  const onProjectButtonClick = () => {

  }

  return (
    <div>
      <div className="overlay d-flex flex-wrap">
      <div className="drawer">
        <div className="hideOverlay"><img onClick={hideOverlay} src="./img/deleteButton.svg" alt="back" /></div>
        <div className="overlayTitle text-center flex-wrap"><h1>{title}</h1></div>
        <div className="d-flex align-start justify-center flex-wrap">
          <div  className="sliderWrapper ">
            <AwesomeSlider  fillParent={false} bullets={false} animation="openAnimation" cssModule={AwesomeSliderStyles} >
              {images.map( image => <div data-src={image} />)}
            </AwesomeSlider>  
          </div> 
           
        </div> 
        <div className="overlayContent d-flex flex flex-wrap mt-10 ">
            
            <div className="description d-flex flex flex-column justify-center align-center flex-wrap">
              <h2>Description</h2>
              <div className="d-flex flex justify-center align-center">
                <p className="text-left">{fullDescription}</p>
              </div>
            </div>

            
              <div className="technologies d-flex flex justify-center align-center flex-wrap ">
                <h2 >Technologies</h2>
              </div>
              <ul>
                {technologies.map(t => <li className="mb-5"><b>{t}</b></li>)}
              </ul>
            
            
          </div> 
        <div className="cardButtons">
          <div className="gitButtons">
          <a href={gitHubLink} target="_blank"><button className="githubButton">Look on gitHub</button></a>
            { projectLink ? <a href={projectLink} target="_blank" ><button className="githubButtonGreen">Start App</button></a> 
                          : <>
                              <button className="githubButtonGrey">You can't start it</button>
                              <p>The application requires special servers or expensive services :(</p>
                            </>}
          </div>
          
        </div>
      </div>
    </div>
    </div>
  )
}

export default CardOverlay
