import Header from "./components/Header";
import Home from "./pages/Home";
import { BrowserRouter as Router } from 'react-router-dom';
import AboutMe from "./components/aboutMeOverlay.js/AboutMe";
import { useRef, useState } from "react";
import Contacts from "./components/Contacts/Contacts";


function App() {

  
  const [ isVisAboutMe, setIsVisAboutMe] = useState(false)
  const hideAboutMe = () => setIsVisAboutMe(false)

  const [ isVisContacts, setIsVisContacts] = useState(false)
  const hideContacts = () => setIsVisContacts(false)

  return (
    <Router>
      <div className="wrapper clear">
        
          <Header onAboutMeClick={() => {setIsVisAboutMe(true)}}
                  onContactsClick={() => {setIsVisContacts(true)}}/>

          {isVisAboutMe && (<AboutMe  hideAboutMe={hideAboutMe}/>)}  
          {isVisContacts && (<Contacts  hideContacts={hideContacts}/>)} 

          <Home />      
        
      </div>
    </Router>
  );
}

export default App;
